<template>
    <!--
				Card - Blog
    -->
    <div class="card-inner blog" :class="{'active':$route.name=='Blog'}" id="blog-card">
        <div class="card-wrap">
            <!--
						Blog
            -->
            <div class="content blog">
                <!-- title -->
                <div class="title">生活</div>

                <!-- content -->

                <div class="row border-line-v">
                    <!-- blog item -->
                    <template v-for="(item, index) in blogList">
                        <div :key="index" class="col col-d-12 col-t-12 col-m-12 border-line-h">
                            <div class="box-item">
                                <div class="image">
                                    <a :href="item.link" target="_blank">
                                        <img :src="require('@/assets/images/blog/'+item.img)" alt />
                                        <span class="info">
                                            <span class="fas fa-link"></span>
                                        </span>
                                        <span class="date">
                                            <strong v-text="item.year"></strong>
                                            <span v-text="item.month"></span>
                                        </span>
                                    </a>
                                </div>
                                <div class="desc">
                                    <a :href="item.link" target="_blank" class="name" v-text="item.des"></a>
                                    <div class="category" v-text="item.type"></div>
                                </div>
                            </div>
                        </div>
                    </template>

                    <div class="clear"></div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            blogList: [
                {
                    month: "Aug",
                    year: "2019",
                    img: "modernWeb19.jpg",
                    des: "Modern Web 2019",
                    type: "興趣",
                    link: "https://modernweb.tw/"
                },
                {
                    month: "Jul",
                    year: "2019",
                    img: "yushin.jpg",
                    des: "玉山攻頂",
                    type: "挑戰",
                    link: "https://www.instagram.com/p/B0QSm9clqI4/"
                },
                {
                    month: "Apr",
                    year: "2019",
                    img: "cheer.jpg",
                    des: "全國啦啦隊系統技術人員",
                    type: "榮譽",
                    link: "https://www.facebook.com/photo.php?fbid=2379702518715981&set=a.830604443625804&type=3&theater"
                },
                {
                    month: "Nov",
                    year: "2018",
                    img: "ithelp.jpg",
                    des: "IT幫鐵人賽30天技術文",
                    type: "挑戰",
                    link: "https://ithelp.ithome.com.tw/users/20107585/ironman/1602"
                },
                {
                    month: "Oct",
                    year: "2018",
                    img: "hacker.jpg",
                    des: "黑客松智慧城市",
                    type: "興趣",
                    link: "https://smartcityhackathon2018.alysida.io/"
                }
            ]
        };
    }
};
</script>

<style lang="scss" scoped>
</style>


