<template>
    <div class="card-inner" :class="{'active':$route.name=='Works'}" id="works-card">
        <div class="card-wrap">
            <!--
						Works
            -->
            <div class="content works">
                <!-- title -->
                <div class="title">經歷</div>

                <!-- filters -->
                <div class="filter-menu filter-button-group">
                    <div class="f_btn" :class="{'active':item.value==showType}" v-for="(item,index) in showList" :key="index">
                        <div name="showType" v-text="item.name" @click="showType = item.value"></div>
                    </div>
                </div>

                <div class="row grid-items border-line-v">
                    <transition-group name="fade" mode="in-out">
                        <template v-for="(item, index) in workList">
                            <div
                                :key="index"
                                v-show="showType==item.workType||showType=='all'"
                                class="col col-d-6 col-t-6 col-m-12 grid-item photo border-line-h"
                            >
                                <div class="box-item">
                                    <div @click="showAlbum(item.album)" class="image">
                                        <a class="has-popup-image">
                                            <img :src="require('@/assets/images/works/'+item.img)" alt />
                                            <span class="info">
                                                <span :class="item.hoverImg"></span>
                                            </span>
                                        </a>
                                    </div>
                                    <div class="desc">
                                        <a class="name has-popup-image" v-text="item.name"></a>
                                        <div class="category" v-text="item.workType"></div>
                                    </div>
                                </div>
                            </div>
                        </template>
                    </transition-group>
                </div>
                <VueEasyLightbox :visible="visible" :imgs="imgs" :index="index" @hide="handleHide"></VueEasyLightbox>
                <div class="clear"></div>
            </div>
        </div>
    </div>
</template>

<script>
import VueEasyLightbox from "vue-easy-lightbox";
export default {
    data() {
        return {
            visible: false,
            index: 0, // default
            showType: "all",
            showList: [
                { name: "全部", value: "all" },
                { name: "經歷", value: "work" },
                { name: "活動", value: "activity" },
                { name: "其他", value: "other" }
            ],
            imgs: [],
            workList: [
                {
                    album: "cheer",
                    img: "cheerSys.jpg",
                    hoverImg: "ion ion-image",
                    name: "Minimalism Shapes",
                    workType: "other"
                },
                { album: "smart", img: "smart.jpg", hoverImg: "ion ion-image", name: "Architecture", workType: "activity" },
                {
                    album: "modernWeb19",
                    img: "modernWeb19.jpg",
                    hoverImg: "ion ion-image",
                    name: "Social Website",
                    workType: "activity"
                },
                { album: "o9m", img: "9mLogin.jpg", hoverImg: "ion ion-image", name: "Motorcycle Helmet", workType: "work" },
                { album: "n9m", img: "new9m.jpg", hoverImg: "ion ion-image", name: "Gereal Travels", workType: "work" },
                { album: "ithelp", img: "ithelp.jpg", hoverImg: "ion ion-image", name: "Staircase", workType: "other" },
                { album: "gacc", img: "gacc.jpg", hoverImg: "ion ion-image", name: "Daylight Entrance", workType: "work" }
            ],
            ithelp: []
        };
    },
    methods: {
        showAlbum(album) {
            const _this = this;
            _this.imgs = [];
            for (let i = 0; i < 5; i++) {
                _this.imgs.push(require(`@/assets/images/works/${album}/${album}${i}.jpg`));
            }
            _this.visible = true;
        },
        handleHide() {
            this.visible = false;
        }
    },
    components: {
        VueEasyLightbox
    }
};
</script>

<style lang="scss" scoped>
.fade-enter-active {
    animation: fadeInDown 1s;
}
.fade-leave-active {
    animation: fadeOutUp 1s;
}
</style>


